* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  min-height: 100%;
  width: 100%;
  background: #FAFAFA;
  font-weight: 400;
  color: #444;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'VALORANT', sans-serif;
}
#app {
  height: 100%;
}
header {
  /* background: violet; */
  background: linear-gradient(125deg, #000000e8 54%, #ff4655 50%);
  width: 100%;
  height: 10vh;
}
header nav {
  padding: 0 2rem;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header nav a {
  color: #FAFAFA;
}
.home {
  width: 100%;
  height: 90vh;
  background: #ff4655;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home .logo {
  width: 400px;
  height: 400px;
  background-image: url(valorant-vector-logo.svg);
  background-size: cover;
  background-color: #ff4655;
  animation: anim 0.8s linear forwards;
}
@media screen and (max-width: 520px) {
  .home .logo {
    /* animation: none; */
    width: 220px;
    height: 220px;
  }
}
@keyframes anim {
  0% {
    transform: scale(0.2);
    opacity: 0.1;
  }
  100% {
    transform: scale(1.65);
    opacity: 1;
  }
}
/* all agents style */
section h1 {
  text-align: center;
  font-size: 3rem;
  padding: 1rem;
}
section section.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  /* gap: 20px; */
  padding: 1rem;
}
section.grid article {
  margin: 10px;
  width: 160px;
  height: 160px;
  background-color: #ff4655;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  box-shadow: 5px 5px 0 0px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
section.grid article:hover {
  box-shadow: 10px 10px 0 0px rgba(0, 0, 0, 0.7);
  transform: translate(-5px, -5px);
}
section.grid article h2 {
  color: black;
  -webkit-text-stroke: 1px white;
  text-shadow: 0 0 15px black;
}
/* all agents <540px */
@media screen and (max-width: 520px) {
  section section.grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    /* gap: 20px; */
    padding: 0.4rem;
  }
  section.grid article {
    margin: 10px;
    width: 160px;
    height: 160px;
    background-color: #ff4655;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    box-shadow: 5px 5px 0 0px rgba(0, 0, 0, 0.2);
    transition: 0.5s;
  }
}
/* Individual Agent */
main.agent {
  width: 100%;
  height: 90vh;
  background-image: linear-gradient(to right, #232526, #414345);
}
main.agent .imgBox {
  height: 35%;
  width: 100%;
  background: #414345;
}
main.agent .contentBox {
  height: 60%;
  width: 100%;
  padding: 0.75em 0.5em;
}
/* agent contentBox */
main.agent .contentBox {
  display: flex;
  flex-direction: column;
  font-family: Montserrat;
}
main.agent .contentBox .infoBox,
main.agent .contentBox .abilitiesBox {
  margin: 0.5em 0;
}
main.agent .contentBox h2 {
  font-family: 'Josefin Sans';
  padding: 0.25em 0;
  color: white;
}
main.agent .contentBox .infoBox {
  height: 30%;
}
main.agent .contentBox .infoBox p {
  font-size: 0.85em;
  color: #b9b9b9;
}
main.agent .contentBox .abilitiesBox {
  height: 70%;
}
main.agent .contentBox .abilitiesBox .abilities-list {
  font-size: 0.7em;
  height: 90%;
  overflow-y: auto;
}
main.agent .contentBox .abilitiesBox .abilities-list::-webkit-scrollbar {
  display: none;
}
main.agent .contentBox .abilitiesBox .abilities-list .ability {
  width: 100%;
  background: #5a5a59;
  margin: 0.75em 0;
  padding: 0.75em 0.75em;
  border-radius: 10px;
  color: #e5e5e9;
}
main.agent .contentBox .abilitiesBox .abilities-list .ability {
  width: 99%;
  min-height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-image: linear-gradient(to left, #706e6e, #274046);
}
main.agent .contentBox .abilitiesBox .abilities-list .ability .imgAbl {
  width: 20%;
  height: 80px;
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
  background-color: #9191916b;
  backdrop-filter: blur(5px);
  border-radius: 10px;
}
main.agent .contentBox .abilitiesBox .abilities-list .ability .descAbl {
  min-height: 80px;
  width: 75%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
main.agent .contentBox .abilitiesBox .abilities-list .ability .descAbl h3 {
  padding-bottom: 0.25em;
  color: white;
  letter-spacing: 1px;
}
main.agent .contentBox .abilitiesBox .abilities-list .ability .descAbl p {
  color: #b9b9b9;
}
/* agent .imgBox */
main.agent .imgBox {
  background-position: center;
  background-repeat: no-repeat;
  background-position: 80% 0%;
  position: relative;
  z-index: auto;
}
main.agent .imgBox::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to left, black, transparent 100%);
  backdrop-filter: blur(1px);
  z-index: 0;
}
main.agent .imgBox .bustPicture {
  position: relative;
  height: 100%;
  width: 100%;
  background-size: 70%;
  background-position: -35% 0%;
  background-repeat: no-repeat;
  z-index: 5;
}
main.agent .imgBox .bustPicture h1.name {
  position: absolute;
  right: 0.25em;
  bottom: 20%;
  color: #dfdfdf;
  letter-spacing: 1px;
}
main.agent .imgBox .bustPicture h1.name span {
  font-size: 0.4em;
  font-family: 'Outfit';
  padding: 0.25em 0.5em;
  background: #5a5a5950;
  border-radius: 5px;
}
::selection {
  color: #0d0d20;
  background: #07df07;
}
